/* ===============================
   SERVICES SECTION
================================*/

.services {
  padding: 80px 20px;
}

/* Header */
.services-header h2 {
  font-size: 36px;
  color: #ffffff;
  margin-bottom: 15px;
  margin-left: 100px;
}

.services-header p {
  color: #ffffff;
  font-size: 16px;
  margin-left: 100px;
}

/* ===============================
   HIGHLIGHT SECTION
================================*/

.service-highlight {
  max-width: 1200px;
  margin: 60px auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 40px;
  align-items: center;
}

/* Text */
.service-text h3 {
  font-size: 28px;
  color: #ffffff;
  margin-bottom: 15px;
}

.service-text p {
  color: #ffffff;
  line-height: 1.7;
  margin-bottom: 15px;
}

.service-text ul {
  list-style: none;
  padding: 0;
  margin-bottom: 15px;
}

.service-text ul li {
  margin-bottom: 8px;
  font-size: 15px;
  color: #ffffff;
}

.service-note {
  font-weight: 500;
  color: #ffffff;
}

/* Main Image */
.servise img {
  width: 520px;
  height: 270px;
  object-fit: cover;
  border-radius: 16px;
  display: block;
  margin: auto;
  box-shadow: 0 10px 25px rgba(0,0,0,0.15);
  transition: transform 0.3s ease;
}

.servise img:hover {
  transform: scale(1.05);
}

/* ===============================
   OTHER SERVICE CARDS
================================*/

.other-services {
  max-width: 1200px;
  margin: auto;
  text-align: center;
}

.other-services h3 {
  font-size: 28px;
  margin-bottom: 30px;
  color: #ffffff;
}

.service-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 25px;
}

.service-card {
  padding: 25px;
  border-radius: 16px;
  backdrop-filter: blur(10px);
  background: rgba(0, 0, 0, 0.5);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.25);
  transition: transform 0.3s ease;
}

.service-card:hover {
  transform: translateY(-8px);
}

.service-card h4 {
  color: #ffffff;
  margin-bottom: 10px;
}

.service-card p {
  font-size: 14px;
  color: #ffffff;
}

/* ===============================
   SLIDER SECTION
================================*/

.slider-title {
  text-align: center;
  margin: 100px 0 30px;
  color: #ffffff;
}

.slider-wrapper {
  position: relative;
  width: 90vw;
  left: 50%;
  transform: translateX(-50%);
  overflow: hidden;
  height: 600px;
  display: flex;
  align-items: center;
}

.slider-track {
  display: flex;
  width: 100%;
  height: 100%;
  transition: transform 0.6s ease;
}

.slide-card {
  min-width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  backdrop-filter: blur(10px);
  background: rgba(255,255,255,0.08);
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.3);
}

.slide-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  inset: 0;
  z-index: -1;
  border-radius: 20px;
}

.slide-card h4 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.slide-card p {
  font-size: 1.2rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

/* Buttons */
.slide-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0,0,0,0.6);
  border: none;
  color: white;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 18px;
  z-index: 10;
}

.left { left: 10px; }
.right { right: 10px; }

.slide-btn:hover {
  background: #0077ff;
}

/* ===============================
   RESPONSIVE FIXES (NO DESIGN CHANGE)
================================*/

/* Tablets */
@media (max-width: 992px) {
  .services-header h2,
  .services-header p {
    margin-left: 0;
    text-align: center;
  }

  .servise img {
    width: 420px;
    height: 220px;
  }

  .slider-wrapper {
    height: 420px;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .services {
    padding: 60px 15px;
  }

  .service-highlight {
    text-align: center;
  }

  .servise img {
    width: 260px;
    height: 160px;
  }

  .slider-wrapper {
    height: 300px;
  }

  .slide-card h4 {
    font-size: 1.5rem;
  }

  .slide-card p {
    font-size: 1rem;
  }

  .slide-card {
    padding: 25px;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .slider-wrapper {
    height: 260px;
  }
}
